	module.exports = {"data":{"markdownRemark":{"html":"<p>To get the most out of A Race athlete, learn more about requirements and options</p>\n<h4>A Race athlete Strava Club</h4>\n<h4>Strava Premium</h4>\n<h4>Location</h4>\n<h4>Weight</h4>\n<h4>FTP</h4>\n<hr>\n<p>Some features in A Race athlete require specific data in order for that feature to be useful. If the data is not available, you will see a Feature Requirements notice.</p>\n<p>For example, we can’t show you a Power to Weight history graph if your data contains no FTP or Weight data.</p>\n<h3>Requirements and Options by Features:</h3>\n<h4>Activity Search</h4>\n<p>Non-club members have a limit of the thirty most recent activities to sample Activity Search. A Race athlete club membership is required for full activity indexing for complete Activity Search results.</p>\n<h4>Weekly Stats</h4>\n<p>Non-club members have a limit of the thirty most recent activities to sample Weekly Stats. A Race athlete club membership is required for a full history of Weekly Stats. All users have aggregation charts for time, distance, and elevation. Strava Premium members also have TSS, Kilojoules, Suffer Score, and Calories depending on the data in their activity.</p>\n<h4>Events</h4>\n<p>Non-club members can view and favorite events. A Race athlete club membership is required to create new events.</p>\n<h4>Power at Altitude</h4>\n<p>Strava City and Country entry are used to determine your base altitude. Strava Premium and a FTP entry on Strava are required for FTP.</p>\n<h4>Power and Weight</h4>\n<p>Strava Weight entry is required, Strava Premium and a FTP entry on Strava are optional.</p>\n<hr>\n<h3>Checks and Resolutions</h3>\n<h4>A Race athlete Strava Club membership</h4>\n<p>Anything that requires us to have a history of your Activities requires a club membership. Club members are allowed unlimited Activity storage while non-club members are limited to 21 activities per visit. You can join the <a href=\"https://www.strava.com/clubs/araceathlete\" target=\"_blank\" rel=\"nofollow\">A Race athlete Strava club</a>.</p>\n<h4>Strava Premium membership</h4>\n<p>Much of the advanced analytics data is only available to Strava Premium members, for example, Power and Heart Rate data. <a href=\"https://www.strava.com/premium\" target=\"_blank\" rel=\"nofollow\">Upgrade your Strava account to Premium</a> for this feature.</p>\n<h4>Entry for FTP on Strava</h4>\n<p>The feature requires you have a current <a href=\"https://www.strava.com/settings/performance\" target=\"_blank\" rel=\"nofollow\">FTP on Strava</a>.</p>\n<h4>Entry for Weight on Strava</h4>\n<p>The feature requires you have a current <a href=\"https://www.strava.com/settings/profile\" target=\"_blank\" rel=\"nofollow\">Weight on Strava</a>.</p>\n<h4>Entries for City and Country on Strava</h4>\n<p>The feature requires at least a general idea about your location for calculating a your elevation or time zone. City and Country can be found in your <a href=\"https://www.strava.com/settings/profile\" target=\"_blank\" rel=\"nofollow\">Strava Profile</a>.</p>","frontmatter":{"title":"Feature Requirements","date":"2017-06-05T00:19:00.000Z","path":"/feature-requirements/","parent":"/home/hypersprite/Documents/code/araceathlete-gatsby-material-ui-v1/src/pages/blog-posts/feature-requirements.md absPath of file","tags":"newbie, activities, events, power, weight","description":null,"cover":null}}},"pathContext":{"slug":"/blog-posts/feature-requirements/"}}

